import React from 'react';
import { Card } from 'primereact/card';
import './Participate.css';
import imagen2 from "../../assets//Fondo.png";
const Participate = () => {
  const steps = [
    {
      imagen: imagen2,
      title: 'Regstrate',
      description: 'Crea tu cuenta y completa tu perfil con tus datos personales'
    },
    {
      imagen: imagen2,
      title: 'Enva tu Aplicacin',
      description: 'Completa y enva la solicitud con los requisitos necesarios'
    },
    {
      imagen: imagen2,
      title: 'Participa',
      description: 'Compite con otros participantes y demuestra tus habilidades'
    }
  ];

  return (
    <section id="como-participar" className="como-participar-section">
      <p className='title-participate'>Cmo Participar?</p>
      <p className='text-participate'>Seccin que explica de manera muy breve las diferentes fases del evento.</p>
      <div className="participar-cards-container">
        <div style={{ minWidth: '20px' }}></div>
        {steps.map((step, index) => (
          <Card key={index} className="participar-card">
            <h3>{step.title}</h3>
            <div className="card-icon">
              <img className='imageCard' src={step.imagen} alt={step.title} />
            </div>

            <p className='card-text-participate'>{step.description}</p>
          </Card>
        ))}
        <div style={{ minWidth: '20px' }}></div>
      </div>
      <p className='text-participate' style={{color:"grey", paddingLeft: "25px", paddingRight: "25px"}}>*Si hay ms registros que cupos disponibles, la seleccin se har segn pas/cdigo postal y nivel de engagement. Pases participantes: Mxico, Argentina, Chile, Colombia y Per. El sexto equipo estar formado por jugadores de Latinoamrica y el Caribe.</p>
    </section>
  );
};

export default Participate;
